import { Controller, Inline } from '@ali/4ever-cangjie';
import { isLinkCardNode } from '../utils';

export interface IsLinkCardSelectedProps {
  node: Inline;
}

export default function isLinkCardSelected(controller: Controller, { node }: IsLinkCardSelectedProps) {
  const { selection, document } = controller.value;
  const isSelected = document.isNodeInRange(node.key, selection)
  const isLinkCard = isLinkCardNode(node);
  return isLinkCard &&
    isSelected &&
    selection.isCollapsed &&
    controller.value.isFocused;
}
